import {
  Component,
  OnInit,
  ViewEncapsulation,
  OnDestroy,
  EventEmitter,
  Injector,
} from '@angular/core';
import { Subscription } from 'rxjs';

import { defaultIcons } from './interfaces/icons';
import { NotifyEvent } from './interfaces/notify-event.type';
import { HolderOptions } from './interfaces/holder.options';
import { Notify } from './interfaces/notify.type';
import { NotifyComponent } from './notify.component';
import { NotifyOptions } from './notify.types';
import { NotifyService } from './notify.service';

@Component({
  selector: 'notify-holder',
  template: `<div class="notifies" [ngStyle]="styles" [ngClass]="options.position" [class]="options.className"> <notify *ngFor="let n of notifies; let i = index" [item]="n"></notify> </div> `,
  styles: [`.notifies {
  position: fixed;
}
.notify {
  width: 100%;
  box-sizing: border-box;
  position: relative;
  margin-bottom: 10px;
  color: #fff;
  cursor: pointer;
  transition: all 0.5s;
}
.notify-theme-default {
  padding: 10px 20px;
}
.notify-theme-default.notify-alert {
  background: #ffdb5b;
}
.notify-theme-default.notify-alert .notify-progress span {
  background: #edc242;
}
.notify-theme-default.notify-success {
  background: #8bc34a;
}
.notify-theme-default.notify-success .notify-progress span {
  background: #689f38;
}
.notify-theme-default.notify-error {
  background: #f44336;
}
.notify-theme-default.notify-error .notify-progress span {
  background: #d32f2f;
}
.notify-theme-default.notify-info {
  background: #03a9f4;
}
.notify-theme-default.notify-info .notify-progress span {
  background: #0288d1;
}
.notify-theme-bootstrap.notify-alert .notify-progress span {
  background: #edc242;
}
.notify-theme-bootstrap.notify-success .notify-progress span {
  background: #689f38;
}
.notify-theme-bootstrap.notify-error .notify-progress span {
  background: #d32f2f;
}
.notify-theme-bootstrap.notify-info .notify-progress span {
  background: #0288d1;
}
.notify-title {
  margin: 0;
  padding: 0;
  line-height: 30px;
  font-size: 16px;
  font-weight: bold;
}
.notify-content {
  margin: 0;
  font-size: 14px;
  padding: 0 50px 0 0;
  line-height: 20px;
}
.notify-icon {
  position: absolute;
  box-sizing: border-box;
  top: 0;
  right: 0;
  height: 100%;
  max-width: 70px;
  width: 100%;
}
.notify-icon svg {
  width: 100%;
  height: 100%;
  padding: 10px;
  fill: #fff;
}
.notify-icon > * {
  max-width: 70px;
}
.notify-progress {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 5px;
}
.notify-progress span {
  float: left;
  height: 100%;
}
.notify-rtl {
  direction: rtl;
}
.notify-rtl .notify-title {
  padding-left: 50px;
}
.notify-rtl .notify-content {
  padding: 0 0 0 50px;
}
.notify-rtl .notify-icon {
  left: 0;
  right: auto;
}
.notify-fade-in {
  animation-duration: 1s;
  animation-fill-mode: both;
  -webkit-animation-name: fadeIn;
  animation-name: fadeIn;
}
.notify-fade-out {
  animation-duration: 1s;
  animation-fill-mode: both;
  -webkit-animation-name: fadeOut;
  animation-name: fadeOut;
}
@-webkit-keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@-webkit-keyframes fadeOut {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
@keyframes fadeOut {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
`],
  encapsulation: ViewEncapsulation.None,
  preserveWhitespaces: false
})
export class NotifyHolderComponent implements OnInit, OnDestroy {
  notifies: Notify[] = [];
  options: HolderOptions;
  styles: any = {};

  private listener: Subscription;

  constructor(injector: Injector) {
    this.listener = injector
      .get(NotifyService)
      .getChangeEmitter()
      .subscribe(item => {
        switch (item.command) {
          case 'clear':
            this.notifies = [];
            break;
          case 'remove':
            this.remove(item.id!);
            break;
          case 'set':
            if (item.add) this.add(item.notify!);
            else this.defaultBehavior(item);
            break;
          case 'options':
            this.options = Object.assign(
              {
                lastOnBottom: true,
                minWidth: 300,
                maxWidth: 300,
                maxStack: 8,
                position: ['right', 'bottom'],
                offset: [20, 20],
                zIndex: 1031,
              },
              item.holderOptions!,
            );
            this.styles = {
              'z-index': this.options.zIndex,
              'min-width.px': this.options.minWidth,
              'max-width.px': this.options.maxWidth,
            };
            this.styles[
              this.options.position[0] + '.px'
            ] = this.options.offset[0];
            this.styles[
              this.options.position[1] + '.px'
            ] = this.options.offset[1];
            break;
          default:
            this.defaultBehavior(item);
            break;
        }
      });
  }

  ngOnInit() {}

  private defaultBehavior(value: NotifyEvent) {
    this.notifies.splice(this.notifies.indexOf(value.notify), 1);

    if (value.notify.onDestroy || this.options.onDestroy) {
      const emitItem = this.getEmit(value.notify, false);
      if (value.notify.onDestroy) value.notify.onDestroy(emitItem);

      if (this.options.onDestroy) this.options.onDestroy(emitItem);
    }
  }

  private getEmit(notify: Notify, to: boolean) {
    const res: Notify = {
      createdOn: notify.createdOn,
      id: notify.id,
      type: notify.type,
      icon: notify.icon,
      title: notify.title,
      content: notify.content,
      html: notify.html,
    };
    if (!to) res.destroyedOn = new Date();
    return res;
  }

  private remove(id: string) {
    let index = 0,
      item: Notify = null,
      allow = false;

    this.notifies.forEach((notify, idx) => {
      if (notify.id === id) {
        index = idx;
        allow = true;
        item = notify;
      }
    });

    if (allow) {
      this.notifies.splice(index, 1);
    }
  }

  private add(item: Notify) {
    item = Object.assign(
      {
        type: 'success',
        timeout: 1000 * 3,
        pauseOnHover: true,
        progress: false,
        clickToClose: true,
        theme: 'default',
        animate_in: 'notify-fade-in',
        animate_out: 'notify-fade-out',
        createdOn: new Date(),
      },
      item,
    );

    if (!item.html) {
      switch (item.theme) {
        case 'bootstrap':
          item.html = `<div class="alert alert-{bstype}" role="alert"><strong>{title}</strong> {content}</div>`;
          break;
        default:
          item.html = `<div class="notify-title">{title}</div><div class="notify-content">{content}</div><div class="notify-icon">{icon}</div>`;
          break;
      }
    }

    if (this.options.lastOnBottom) {
      if (this.notifies.length >= this.options.maxStack)
        this.notifies.splice(0, 1);
      this.notifies.push(item);
    } else {
      if (this.notifies.length >= this.options.maxStack)
        this.notifies.splice(this.notifies.length - 1, 1);

      this.notifies.splice(0, 0, item);
    }

    if (item.onCreate || this.options.onCreate) {
      const emitItem = this.getEmit(item, true);
      if (item.onCreate) item.onCreate(emitItem);

      if (this.options.onCreate) this.options.onCreate(emitItem);
    }
  }

  ngOnDestroy(): void {
    if (this.listener) this.listener.unsubscribe();
  }
}
